<template>
  <div class="list">
    <div v-title>Blog</div>
    <!-- header -->
    <header>
      <div class="leftHead">
        <img src="~assets/img/logo.png" alt="logo" />
        <p>BLOG</p>
      </div>
      <div class="centerHead">
        <my-search-box @search="search"></my-search-box>
      </div>
      <div class="rightHead">
        <!-- <router-link to="/blog/add"> -->
        <p class="add" @click="addBlog">Add a blog</p>
        <!-- </router-link> -->
        <router-link to="/my">
          <img :src="avatar" />
        </router-link>
        <p v-cloak>{{ username }}</p>
      </div>
    </header>
    <!-- main content -->
    <section class="main">
      <ul class="tags">
        <li v-for="(tag, index) in tags" :key="index" @click="tagSearch(index)">
          {{ tag }}
        </li>
      </ul>
      <div class="content">
        <my-swiper class="banner" :loop="true" :key="bannerList.length">
          <div
            class="swiper-slide"
            v-for="(banner, index) in bannerList"
            :key="index"
          >
            <img :src="banner" class="bannerImg" />
          </div>
        </my-swiper>
        <div class="blogListWrapper">
          <ul class="blogList" v-if="isHasResult">
            <li v-for="(blog, index) in blogList" :key="index">
              <div class="title" v-cloak @click="selectBlog(blog)">
                {{ blog.title }}
              </div>
              <div class="blogContent">
                <p v-cloak>{{ blog.content }}</p>
              </div>
              <div class="info">
                <div class="infoLeft">
                  <!-- 这个地方要改 -->
                  <img v-cloak :src="avatar" alt="" />
                  <p v-cloak>{{ blog.author }}</p>
                </div>
                <div class="infoRight">
                  <img
                    v-if="blog.userLike"
                    src="~assets/img/heartR.png"
                    alt=""
                  />
                  <img v-else src="~assets/img/heartR.png" alt="" />
                  <p v-cloak>{{ blog.collection_num }}</p>
                </div>
              </div>
            </li>
          </ul>
          <div v-else class="noneResults">
            <img src="~assets/img/noneResult.png" alt="" />
            <img src="~assets/img/404Bgc.png" alt="" />
          </div>
        </div>
      </div>
    </section>
    <router-view></router-view>
  </div>
</template>
<script>
import { getList } from 'api/blog'
import MySwiper from 'base/mySwiper.vue'
import MySearchBox from 'base/mySearchBox.vue'
import { CURRENTBLOG_KEY } from '@/config'
import storage from 'assets/js/storage'
export default {
  name: 'list',
  data() {
    return {
      username: '',
      avatar: require('assets/userAvatar/15820256995611.jpg'),
      tags: ['Python', 'JS', 'PHP', 'HTML', 'CSS', 'Vue.js'],
      isHasResult: true,
      blogList: [],
    }
  },
  created() {
    this.bannerList = [
      require('assets/img/banner3.jpg'),
      require('assets/img/bgc.jpg'),
      require('assets/img/banner2.jpg'),
    ]
    this.getBlogList()
  },
  methods: {
    addBlog() {
      this.$router.push({ path: `/add` })
    },
    selectBlog(blog) {
      storage.set(CURRENTBLOG_KEY, blog)
      // this.$router.push({ path: `/blog/${blog.id}`})
      // open in a new window
      let routerUrl = this.$router.resolve({
        path: `/blog/${blog.id}`,
      })
      window.open(routerUrl.href, '_blank')
    },
    tagSearch(index) {
      this.search(this.tags[index])
    },
    search(query) {
      this.getBlogList(query)
    },
    getBlogList(keyword = '') {
      getList('', keyword).then((res) => {
        if (res && res.errno === 0) {
          this.isHasResult = true
          this.blogList = res.data
        } else {
          this.isHasResult = false
        }
      })
    },
  },
  components: {
    MySwiper,
    MySearchBox,
  },
}
</script>
<style lang="scss" scoped>
@import '~assets/scss/mixins';
@media screen and (min-width: 720px) {
  .list {
    width: 100vw;
    height: 100vh;
    background-color: rgb(240, 240, 240);
    header {
      width: 80%;
      height: 60px;
      background-color: white;
      @include flex-between();
      padding: 0 10%;
      box-shadow: 1px 0px 5px rgba(0, 0, 0, 0.2);
      position: fixed;
      left: 0;
      top: 0;

      .leftHead {
        width: 10%;
        height: 100%;
        min-width: 100px;
        font-size: 20px;
        @include flex-start();
        img {
          height: 30px;
          margin-right: 10px;
        }
      }
      .centerHead {
        flex: 1;
        height: 100%;
        @include flex-end();
        .searchBox {
          width: 96%;
          height: 36px;
          margin: 12px 0;
        }
      }
      .rightHead {
        width: 20%;
        min-width: 150px;
        height: 80%;
        margin: 10% 0;
        @include flex-end();
        .add {
          height: 26px;
          line-height: 26px;
          text-align: center;
          padding: 0 10px;
          background-color: #00a1d6;
          color: white;
          border-radius: 5px;
          font-size: 14px;
          margin-right: 25px;
        }
        img {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          margin-right: 5px;
        }
      }
    }
    .main {
      width: 80%;
      height: calc(100% - 90px);
      margin: 75px auto 0;
      display: flex;
      justify-content: space-between;
      box-shadow: 1px 0px 5px rgba(0, 0, 0, 0.2);
      overflow: hidden;
      .tags {
        width: 10%;
        height: 100%;
        background-color: white;
        li {
          width: 100%;
          height: 40px;
          line-height: 40px;
          text-align: center;
          @include ellipsis();
          &:hover {
            cursor: pointer;
            background-color: rgb(227, 233, 231);
          }
        }
      }
      .content {
        width: calc(90% - 15px);
        height: 100%;
        .banner {
          width: 100%;
          height: 100px;
          margin-bottom: 10px;
          .bannerImg {
            width: 100%;
          }
        }
        .blogListWrapper {
          width: 94%;
          height: calc(100% - 120px);
          padding: 5px 3%;
          background-color: white;
          overflow-y: auto;
          .blogList {
            width: 100%;
            &::-webkit-scrollbar {
              display: none;
            }
            li {
              width: 100%;
              padding: 15px 0 10px;
              border-bottom: 1px solid #575757;
              &:last-child {
                border-bottom: none;
              }
              .title {
                font-size: 18px;
                font-weight: 600;
                &:hover {
                  cursor: pointer;
                }
              }
              .blogContent {
                width: 100%;
                line-height: 20px;
                margin: 5px 0;
                color: #575757;
                @include multiline-ellipsis(3);
              }
              .info {
                width: 100%;
                height: 30px;
                @include flex-between();
                .infoLeft {
                  @include flex-start();
                  img {
                    width: 25px;
                    height: 25px;
                    border-radius: 50%;
                    margin-right: 5px;
                  }
                  p {
                    font-size: 14px;
                    color: #575757;
                    user-select: none;
                  }
                }
                .infoRight {
                  @include flex-end();
                  img {
                    width: 18px;
                    height: 18px;
                    margin-right: 5px;
                    &:hover {
                      cursor: pointer;
                    }
                  }
                  p {
                    font-size: 14px;
                    color: #575757;
                    user-select: none;
                  }
                }
              }
            }
          }
          .noneResults {
            width: 94%;
            // height: calc(100% - 120px);
            padding: 5px 3%;
            background-color: white;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: space-between;
            img:nth-child(1) {
              width: 100%;
            }
            img:nth-child(2) {
              height: 200px;
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 719px) {
}
</style>
